  // Used for show add company activity form while clicking on add company activity button
  AddCompanyActivities() {
    this.iShowCompanyActivitiesPopup = true;

    let today = new Date();
    this.minDate = { year: today.getFullYear() - 0, month: today.getMonth() + 1, day: today.getDate() };

    let month = (today.getMonth() + 1);
    
    var d = new Date(today.getFullYear(), today.getMonth(), today.getDate());
    d.setFullYear(d.getFullYear() + 1);
    let nDay = d.getDate();
    let permitExpiryDate = (nDay > 9 ? nDay : ('0' + nDay)) + '-' + (month > 9 ? month : ('0' + month)) + '-' + d.getFullYear();
    this.CompanyActivitiesForm = this.formBuilder.group({
      activityID: ['', [Validators.required]],
      fees: ['', [Validators.required]],
      activitieExpiryDate: [permitExpiryDate, [Validators.required]]
    });
  }

