  // Used for Delete/Disable Company Activity
  async deleteCompanyActivities(input, CompanyActivitie) {     
    let updateCompanyInfo = this.common.getValueFromSessionStorage('updateCompanyInfo');
    if (input == 1) {
      this.disableActivityCompanyId = updateCompanyInfo.CompanyID;
      const WantDetete = await this.translate.get('AreYouSuretoDeleteActivitie').toPromise();
      const YES = await this.translate.get('YES').toPromise();
      const NO = await this.translate.get('NO').toPromise();
      this.confirmationDialogService.confirm('Confirm', WantDetete, YES, NO)
        .then((confirmed) => {
          if (confirmed) {
            if (CompanyActivitie != null) {
              this.disableActivityInfo.push(CompanyActivitie);
            }
          }
        });
    } else {
      for (var val of this.disableActivityInfo) {
        let userLoginInfo = this.common.getValueFromSessionStorage('userLoginInfo');
        let jsonData: any = {};
        jsonData.UserID = userLoginInfo.ID;
        jsonData.Token = userLoginInfo.Token;
        jsonData.ComUpdateRequestID = CompanyActivitie;
        jsonData.CompanyID = this.disableActivityCompanyId;
        jsonData.ActivityID = val.ID;
        jsonData.Fees = val.Fees;
        jsonData.ExpiryDate = val.ExpiryDate.split("-").reverse().join("-");
        let outerJsonData: any = {};
        outerJsonData.inputParameter = JSON.stringify(jsonData);
        let APIEndPoint: string = '';
        APIEndPoint = 'api/CompanyManager/CompanyUpdateRequestDisableActivity';
        this.apiCall.postAPICallWithToken(APIEndPoint, outerJsonData).then(result => {
          this.resultOutput = result;
        }).catch(error => {
          console.log("error ", error);
        });
      }
    }
  }

